
import axios from 'axios';
import * as qs from 'querystring';

const querystring = qs.parse(window.location.search.slice(1));

axios.get(`https://api.qccareerschool.com/enrollments/${querystring.id}`, {
  params: { code: querystring.code },
}).then((response) => {
  const data = response.data;
  const formData = {
    courses: data.courseCodes,
    title: data.title,
    firstName: data.firstName,
    lastName: data.lastName,
    emailAddress: data.emailAddress,
    telephoneNumber: data.telephoneNumber,
    address1: data.address1,
    address2: data.address2,
    city: data.city,
    provinceCode: data.provinceCode,
    postalCode: data.postalCode,
    countryCode: data.countryCode,
    paymentPlan: data.paymentPlan,
    paymentDay: data.paymentDay,
  };
  window.sessionStorage.setItem('form', JSON.stringify(formData));
  window.location.replace(data.url);
}).catch(console.error);
